
import { useState } from "react";
import { Card, CardContent, Typography, Button, Collapse, Avatar, Box, styled } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ExpandLessIcon from "@mui/icons-material/ExpandLess";
import founder from '../assets/founder.jpg';

export default function Founder() {
  const [expanded, setExpanded] = useState(false);

  const RoundedImage = styled('img')({
    width: '205px',
    height: '250px',
    borderRadius: '5%',
    objectFit: 'cover', 
  });

  return (
    <Box sx={{ padding: 5, flexDirection: 'column', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
      <Typography variant="h6"  sx={{ my: 3, color: '#E87F11', textAlign: 'left', fontSize: { xs: '1.75rem', md: '2rem' } }}>
        <strong></strong>
      </Typography>

      <Box>
        <RoundedImage src={founder} alt="Co-founder" />
      </Box>

      <Typography variant="body1" sx={{ mt: 3, color: '#E87F11',maxWidth: '2000px',
      mx: '3', textAlign: 'left',fontSize: { xs: '1rem', md: '1.25rem' } }}>
        <strong>  </strong>
        <br />
         / 
        <br />
        
        <br /><strong></strong>
        <br />
        THINK7
        <Typography variant="body1" sx={{ mt: 3, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
        <strong></strong>
        </Typography>
       
          90
        <br />
          
        <br />
          
        <br />
          
        <br />
          
        <br />
        
      </Typography>

      <Collapse in={expanded} timeout="auto" unmountOnExit sx={{maxWidth: '2000px',
      mx: '3'}}>
        <br /><Typography variant="body1" sx={{ mt: 3, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
          <strong></strong>
        </Typography>
        <Typography variant="body1" sx={{ mt: 1, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
          10
          <br />
          
          <br />
          <br />
          
          <br />
          <br />
          
          
        </Typography>

        <Typography variant="body1" sx={{ mt: 3, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
          <strong></strong>
        </Typography>
        <Typography variant="body1" sx={{ mt: 1, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
          5%
          <br />
          <br />
          
        </Typography>

        <br /><Typography variant="body1" sx={{ mt: 3, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
          <strong></strong>
        </Typography>
        <Typography variant="body1" sx={{ mt: 1, color: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}>
          
          <br />
          
        </Typography>
      </Collapse>

      <Button
        variant="contained"
        endIcon={expanded ? <ExpandLessIcon /> : <ExpandMoreIcon />}
        onClick={() => setExpanded(!expanded)}
        sx={{ mt: 2, bgcolor: '#E87F11', fontSize: { xs: '1rem', md: '1.25rem' } }}
      >
        {expanded ? "" : ""}
      </Button>
    </Box>
  );
}