import React from 'react';
import { Box, Typography } from '@mui/material';
import { styled } from '@mui/system';
import service from '../assets/philosophy.jpg';

const RoundedImage = styled('img')({
    width: '350px',
    maxWidth: '350px',
    height: '270px',
    borderRadius: '5%',
});

const Philosophy = () => {
    return (
        <Box
            sx={{
                paddingY: 5,
                px: { xs: 2, md: 5 }, // Ensuring equal left and right padding for mobile view
                flexDirection: 'column',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
            }}
        >
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: { xs: 'column', md: 'row' },
                    gap: 4,
                    justifyContent: 'center',
                    alignItems: 'center',
                    width: '100%',
                }}
            >
                <Box
                    flexGrow={1}
                    display="flex"
                    justifyContent="center"
                    alignItems="center"
                    padding={2}
                >
                    <RoundedImage src={service} alt="Personal Gym" />
                </Box>

                <Box
                    flexGrow={1}
                    display="flex"
                    flexDirection="column"
                    justifyContent="center"
                    alignItems="flex-start"
                    px={{ xs: 2, md: 5 }} // Equal padding for mobile view
                >
                    <Typography
                        variant="h5"
                        sx={{
                            color: '#E87F11',
                            fontWeight: 'bold',
                            marginBottom: 1,
                            fontSize: {
                                xs: '1.75rem',
                                md: '2rem',
                            },
                        }}
                    >
                        
                    </Typography>
                    <Typography
                        variant="body1"
                        sx={{
                            color: '#E87F11',
                            lineHeight: 1.6,
                            fontSize: {
                                xs: '1rem',
                                md: '1.25rem',
                            },
                        }}
                    >
                        506070
                        <br />
                        5070
                        <br />
                        <br />
                        
                        <br />
                        - 
                        <br />
                        - 
                        <br />
                        - 
                        <br />
                        
                        <br />
                        
                    </Typography>
                </Box>
            </Box>

            <Box px={{ xs: 2, md: 5 }} marginTop={1}>
                <Typography
                    variant="body1"
                    sx={{
                        color: '#E87F11',
                        lineHeight: 1.6,
                        fontSize: {
                            xs: '1rem',
                            md: '1.25rem',
                        },
                    }}
                >
                    5070
                    
                    
                </Typography>
            </Box>
        </Box>
    );
};

export default Philosophy;
